<template>
  <div class="c-app flex-row align-items-center">
    
    <CContainer >
      <pulse-loader class="spinner" v-if="isAuthing" :loading="isAuthing"></pulse-loader>
      <CRow v-if="!isAuthing" class="justify-content-center">
        <CCol md="8">
          <CCardGroup>
            <CCard class="p-4">
              <CCardBody>
                <CForm>
                  <h1>Login</h1>
                  <p class="text-muted">Sign In to your account</p>
                  <CInput
                    placeholder="Username"
                    autocomplete="username email"
                    v-model="username"
                  >
                    <template #prepend-content><CIcon name="cil-user"/></template>
                  </CInput>
                  <CInput
                    placeholder="Password"
                    type="password"
                    autocomplete="curent-password"
                    v-model="password"
                  >
                    <template #prepend-content><CIcon name="cil-lock-locked"/></template>
                  </CInput>
                  <CRow>
                    <CCol col="6" class="text-left">
                      <CButton color="primary" class="px-4" v-on:click="onLogin">Login</CButton>
                    </CCol>                    
                  </CRow>
                </CForm>
              </CCardBody>
            </CCard>            
          </CCardGroup>
        </CCol>
      </CRow>
    </CContainer>
  </div>
</template>

<script>
import PulseLoader from 'vue-spinner/src/PulseLoader.vue'

export default {
  name: 'Login',
  components: { PulseLoader },
  data() {
    return {
      username: "",
      password: ""
    }
  },
  computed: {
      isAuthing() { return this.$store.state.auth.isAuthing },      
  },

  methods : {
    onLogin() {
      this.$store.dispatch('auth/signIn', { username : this.username, password: this.password });      
    }
  },
  mounted: function () {
    window.localStorage.clear();
  }
}
</script>
<style>
.spinner {
  display: block;
  position: fixed;
  z-index: 1031; /* High z-index so it is on top of the page */
  top: 50%;
  right: 50%; /* or: left: 50%; */
  margin-top: -..px; /* half of the elements height */
  margin-right: -..px; /* half of the elements widht */
}
</style>